import React from "react"
import styles from "../styles/index.module.css"
import Background from "../components/background"
import Toolbar from "../components/toolbar"
import Work from "../components/work"
import Thoughts from "../components/thoughts"
import Footer from "../components/footer"
import tstyles from "../styles/thoughts.module.css"
import fstyles from "../styles/footer.module.css"

export default () => (
    <Background>
        <Toolbar/>
        <div className={styles.titleText} style={{width: "807px"}}> A speculative design studio</div>
        <Work top="600px"/>
        <Thoughts classname={tstyles.index}/>
        <Footer classname={fstyles.index}/>
    </Background>
)